<template>
    <div>
        <loader :is-visible="isLoading"></loader>
        <LeftColumn />
        <div class="right-content">
            <div class="padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('CurrencyManagement') }}</span>
                </div>
            </div>
            <div class="row">
                <div class="col-lg-4">
                    <div class="form-group mt-2">
                        <label>{{ $t("CompanyBaseCurrency") }}:<span class="text-danger">*</span></label>
                        <select class="form-control mr-3" name="select" v-model="selectedCurrency"
                            @change="ChangeCurrencyUpdate(selectedCurrency)">
                            <option value="" disabled :selected="selectedCurrency">Select</option>
                            <option v-for="option in CurrencyList" :value="option.value">{{ option.name }}</option>
                        </select>
                    </div>
                </div>
                <div class="listing mt-0 px-3">
                    <div class="table-responsive">
                        <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                            id="tbContainer">
                            <thead class="thead-bg">
                                <tr>
                                    <th v-for="(item, index) in Headers" :key="index">{{ item }}:</th>
                                    <!-- <th>Automatic:</th> -->
                                    <th class="text-center single-action" colspan="2">
                                        <div @click="AddRow()"
                                            class="round-icon-small btn-success-light d-block float-none m-auto cursor-pointer"
                                            style="line-height: 33px;" title="Add">
                                            <em class="fa fa-plus"></em>
                                        </div>
                                    </th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr class="mainleveldiv" v-for="(item, index) in CurrencyManagementData" :key="index">
                                    <td class="d-flex">
                                        <select class="form-control mr-3" name="select"
                                            v-model="item.source_currency_id" :name="'select_' + index">
                                            <option>Select</option>
                                            <option v-for="option in CurrencyList" :value="option.value">{{ option.name
                                                }}</option>
                                        </select>
                                        <input type="text" class="form-control" placeholder="1" disabled />
                                    </td>
                                    <td>
                                        <select class="form-control mr-3" name="select" v-model="selectedCurrency"
                                            disabled>
                                            <option v-for="option in CurrencyList"
                                                :selected="selectedCurrency == option.value" :value="option.value">{{
                                                    option.name }}</option>
                                        </select>
                                    </td>
                                    <td>
                                        <input type="text" class="form-control" placeholder="Rate"
                                            v-model="item.currency_rate" />
                                    </td>
                                    <td class="text-center">
                                        <div class="form-check form-check-inline  checkbox-inline font-weight-light">
                                            <div class="custom-control custom-checkbox">
                                                <input class="form-check-input custom-control-input"
                                                    v-model="item.Automatic" type="checkbox" :id="'chk_' + index"
                                                    :checked="item.Automatic == 1 ? 'checked' : ''"
                                                    @change="updateAutomaticValue(item)" />
                                                <label class="custom-control-label universal-custom-control-label pt-1"
                                                    :for="'chk_' + index" :title="'Select'"></label>
                                            </div>
                                        </div>
                                    </td>
                                    <td class='text-center single-action'>
                                        <div id='btnRemove' @click="DeleteRow(index, item.currency_log_id)"
                                            class='d-block float-none m-auto cursor-pointer' title='Remove'>
                                            <em class='fa fa-trash-o text-danger'></em>
                                        </div>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div class="float-right mt-3">
                <a title="Save" class="btn btn-success mr-2" v-on:click="saveCurrency"><em class="fa fa-save"></em> {{
                    $t("Save") }}</a>
                <a title="Cancel" class="btn btn-danger "><em class="fa fa-close"></em> {{ $t("Cancel") }}</a>
            </div>
        </div>
        <!---modal popup-->
        <AddCompanySetup :recordId="recordId" :title="$t('CAManageCurrencyManagement')" v-if="showCurrencyManagement"
            v-on:close="showCurrencyManagement = false; recordId = 0"
            v-on:addReloadData="FetchData(); showCurrencyManagement = false; recordId = 0"
            v-bind:moduleName="'CurrencyManagement'" v-bind:emitName="'addReloadData'"
            v-bind:addApiUrl="'/CurrencyManagement/add'" v-bind:subModule="'CurrencyManagement'" />
    </div>
</template>
<script>
import CurrencyManagementDataService from "@/services/DataService";
import Schema from "@/views/Company/CurrencyManagement/CurrencyManagementSchema";
import AddCompanySetup from "@/components/Modals/AddCompanySetup.vue";
import LeftColumn from "../LeftColumn.vue";
import { ShowAlert } from "../../../assets/common/vue-common";
export default {
    components: {
        LeftColumn,
        AddCompanySetup,
    },
    props: {
        sourcetype:
        {
            type: String,
            required: true
        }
    },
    data() {
        return {
            recordId: 0,
            showCurrencyManagement: false,
            noRecord: false,
            isLoading: false,
            CompanyId: null,
            CurrencyManagementData: [],
            CurrencyList: [],
            selectedCurrency: "Select",
            TotalRecords: 0,
            listheaderbuttons: Schema.buttons,
            topSearchFields: Schema.topsearchSchema,
            ShowManageView: false,
            ShowAddCustomReportView: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            listActions: Schema.listActions,
            searchCondition: '',
            conditionForInstantSearch: "",
            SortBy: '',
            SortExp: '',
            showsharereportview: false,
            showemailreportview: false,
            showschedulereportview: false,
            ReportID: null,
            reportname: '',
            reportid: '',
            foldername: '',
            folderid: '',
            LeadStatusList: [],
            searchcondition: '',
            MODULENAME: null,
            SUBMODULECODE: null,
            privilegeParams: null,
            sharetype: '',
            Headers: [],
            filteredHeaders: [],
            StatusListForListing: [
                {
                    STATUS: 'Active',
                    STATUS_ID: 1001,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#28a745"
                }, {
                    STATUS: 'Inactive',
                    STATUS_ID: 1002,
                    STATUS_COLOR: "#ffffff",
                    STATUS_BG_COLOR: "#d72435"
                }
            ],
        }
    },
    async created() {
        var ths = this;
        ths.CompanyId = ths.GetUserInfo.CompanyId;
        ths.privilegeParams = `controller=CurrencyManagement&action=`;
        await ths.FetchData();
        await ths.GetBaseCurrency();
        Schema.CurrencybindCommondropdown();
        // Check if CurrencyManagementData is empty and add a default row if necessary
        if (ths.CurrencyManagementData.length === 0) {
            ths.AddRow();
        }
    },
    computed: {

        filteredUserData() {
            return this.vueObj.UserData.map((item) => {
                const filteredItem = { ...item };
                // Check if USER_EMP_ID is null or empty, then replace with '--'
                if (filteredItem.USER_EMP_ID === null || filteredItem.USER_EMP_ID === '') {
                    filteredItem.USER_EMP_ID = '--';
                }
                return filteredItem;
            });
        },
    },
    mounted() {
        Schema.topsearchSchema.forEach(item => {
            console.log(item);
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        updateAutomaticValue(item) {
            if (item.Automatic) {
                item.Automatic = 1; // Set to 1 when checked
            } else {
                item.Automatic = 0; // Set to 0 when unchecked (if necessary)
            }
        },
        async GetBaseCurrency() {
            var vm = this;
            var params = `configkey=Currency`;
            await CurrencyManagementDataService.GetBaseCurrency(params).then(response => {
                vm.selectedCurrency = response.data.CONFIG_DATA_VALUE;
            }).catch(err => {
                console.log(err);
            })
        },

        saveCurrency: function () {
            var vm = this;
            var objectsArray = [];
            vm.CurrencyManagementData.forEach(item => {
                let parsedItem = {
                    currency_log_id: item.currency_log_id,
                    source_currency_id: item.source_currency_id,
                    destination_currency_id: vm.selectedCurrency,
                    currency_rate: item.currency_rate,
                    created_by: item.created_by.value,
                    status_id: item.status_id.value,
                    company_id: item.company_id,
                    Automatic: item.Automatic ? 1 : 0
                };

                objectsArray.push(parsedItem);
            });
            var obj = {
                postString: JSON.stringify(objectsArray)
            }

            CurrencyManagementDataService.CurrencyManagementData(obj).then(function (response) {
                if (response.data.Result != null && response.data.Result.MAIN_ID > 0) {
                    vm.ShowAlert(vm.$t("SavedSuccessfully", [vm.$t("CurrencyManagement")]), "success", true, vm.$t("Alert"));
                    vm.FetchData();
                }
                else {
                    if (response.data.Result.MAIN_ID == 0) {
                        vm.ShowAlert(vm.$t('ErrorWhileSavingRecords'), "danger", false, vm.$t("Alert"));
                    }

                }
            });
        },
        AddRow: function (e) {
            var vm = this;
            vm.CurrencyManagementData.push({
                "currency_log_id": 0, "source_currency_id": "{\"Name\":null,\"value\":\"1\"}", "destination_currency_id": "{\"Name\":null,\"value\":\"\"}", "created_by": "{\"Name\":null,\"value\":\"\"}", "status_id": "{\"Name\":null,\"value\":\"\"}", "currency_rate": "0.00",
            });
            console.log(vm.CurrencyManagementData);
        },
        DeleteRow(id, currencyLogId) {
            var vm = this;
            if (currencyLogId == 0) {
                vm.CurrencyManagementData.splice(id, 1);
                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CurrencyManagement')]), "Success", true, vm.$t('Alert'));
            }
            else {
                vm.confirmR(vm.$t("ConfirmDeleteRecord"), true, (vm.$t("update") + "  " + vm.$t("status")), function () {
                    var params = `CurrencyLogId=${currencyLogId}`
                    CurrencyManagementDataService.DeleteCurrencyLog(params).then((response) => {
                        if (response.data.STATUS == 0) {
                            vm.ShowAlert(vm.$t('AlreadyUsedCurrency'), "failure", true, vm.$t('Alert'));
                        }
                        else if (response.data.STATUS == 1) {
                            vm.CurrencyManagementData.splice(id, 1);
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('CurrencyManagement')]), "Success", true, vm.$t('Alert'));
                        }
                    });
                });
            }
        },
        ChangeCurrencyUpdate(id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmManageCurrency"), true, false, function (result) {
                ShowAlert
            }, function () {
                vm.cancelAction();
            });
        },
        async cancelAction() {
            var vm = this;
            await vm.FetchData();
            await vm.GetBaseCurrency();
        },
        FetchData: async function () {
            var vueObj = this;
            this.isLoading = true;
            vueObj.CurrencyList = [];
            vueObj.CurrencyList = Schema.topsearchSchema[0].listOptions;
            console.log(vueObj.CurrencyList);
            var url = `CompanyId=${vueObj.CompanyId}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&sortExp=${vueObj.SortExp}&searchQuery=${vueObj.searchCondition.trim()}`;
            await CurrencyManagementDataService.CurrencyManagementListing(url)
                .then(function (response) {
                    const filteredColumnNames = ["source_currency_id", "destination_currency_id", "currency_rate", "Automatic"];
                    // Filter the original list based on the desired COLUMN_NAME values
                    vueObj.filteredHeaders = response.data.FieldSchema.filter(obj => filteredColumnNames.includes(obj.COLUMN_NAME));
                    vueObj.Headers = vueObj.filteredHeaders.map((item) => item.DISPLAY_NAME);
                    vueObj.filteredHeaders.forEach((column) => {
                        column.settings = null;
                        if (column.COLUMN_NAME == "title") {
                            column.settings = {
                                clickEvent: vueObj.clickEventOfColumn,
                                formatter: vueObj.columnDataFormatter,
                            };
                        }
                        if (column.COLUMN_NAME == "status_id") {
                            column.settings = {
                                isInSlot: true
                            };
                        }
                    });
                    if (response.data.Data != "" && response.data.Data != null) {
                        if (response.data.Data.length > 0) {
                            var objectsArray = [];
                            objectsArray = response.data.Data;
                            vueObj.CurrencyManagementData = [];
                            objectsArray.forEach(item => {
                                let parsedItem = {
                                    currency_log_id: item.currency_log_id,
                                    source_currency_id: JSON.parse(item.source_currency_id).value > 0 == true ? JSON.parse(item.source_currency_id).value : '',
                                    destination_currency_id: JSON.parse(item.destination_currency_id).value > 0 == true ? JSON.parse(item.destination_currency_id).value : '',
                                    currency_rate: item.currency_rate.replace("$", "").replace(",", ""),
                                    created_by: JSON.parse(item.created_by),
                                    status_id: JSON.parse(item.status_id),
                                    company_id: item.company_id,
                                    modified_by: JSON.parse(item.modified_by),
                                    modified_at: item.modified_at,
                                    total_records: item.total_records,
                                    Automatic: item.Automatic,
                                    ROWNUMBER: item.ROWNUMBER
                                };

                                vueObj.CurrencyManagementData.push(parsedItem);
                            });
                            // vueObj.selectedCurrency = vueObj.CurrencyManagementData[0].destination_currency_id;
                            vueObj.TotalRecords = vueObj.CurrencyManagementData[0].total_records;
                            vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                            vueObj.CurrentPage = vueObj.PageNumber;
                            vueObj.noRecord = false;
                        } else {
                            vueObj.ReportData = [];
                            vueObj.noRecord = true;
                            vueObj.TotalRecords = 0
                        }
                        if (localStorage.SavedParameters != undefined) {
                            localStorage.removeItem("SavedParameters");
                        }
                    }
                    else {
                        vueObj.noRecord = true;
                        vueObj.ReportData = [];
                        vueObj.TotalRecords = 0
                    }
                    setTimeout(function () {
                        vueObj.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    vueObj.isLoading = false;
                });
        },

    }
}
</script>